@import "../abstracts/variables";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap");
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
  // cursor: none;
}
html,
body {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
